@charset "utf-8";

.vibe{
  width: 50%;
  min-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.change{
  width: 100%;
  min-width: 400px;
  text-align: center;
  border-bottom: 1px solid #3b3b3b;
}
.vibe dl, .change dl{
  width: 100%;
  margin: 40px auto;
  padding: 40px 0 0 0;
  border: 1px solid #a805ad;
  border-radius: 5px;
  box-sizing: border-box;
}
.vibe dt, .change dt{
  display: inline-block;
  width: 50px;
  text-align: right;
  margin-right: 10px;
}
.vibe dd, .change dd{
  display: inline-block;
}
.vibe input, .change input{
  height: 35px;
  margin: 5px 0;
  padding: 0 5px;
  border: 1px solid #a805ad;
  border-radius: 5px;
  box-sizing: border-box;
}
input:focus{
  outline: none !important;
  box-shadow: 0 0 15px #a805ad;
}
.vibe button{
  background: linear-gradient(135deg, #fe0929 0%,#a805ad 50%,#7601f9 100%);
  color: #ffffff90;
  margin: 30px 0;
  padding: 10px 110px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: .3s;
  font-size: 16px;
}
.change button:last-child{
  background: linear-gradient(135deg, #fe0929 0%,#a805ad 50%,#7601f9 100%);
  color: #ffffff90;
  margin: 20px 10px;
  padding: 10px 50px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: .3s;
  font-size: 16px;
}
.change button:first-of-type{
  background: none;
  color: #a805ad;
  margin: 20px 10px;
  padding: 10px 50px;
  border: 1px solid #a805ad;
  border-radius: 5px;
  cursor: pointer;
  transition: .3s;
  font-size: 16px;
}
.vibe button:hover, .change button:hover{
  color: #fff;
}
.product_list{
  width: 50%;
  min-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.product_list img{
  width: 100px;
  margin-right: 20px;
}
.product_list li{
  position: relative;
  display: flex;
  align-items: center;
  padding: 20px 40px 20px 0;
  border-bottom: 1px solid #3b3b3b;
}
.product_list li p{
  font-size: 30px;
  margin: 0 20px;
}
.product_list li div{
  word-wrap: break-word;
  flex-basis: content;
}
.product_list li div p{
  text-align: left;
  font-size: 16px;
  line-height: 30px;
}
.product_list .trash{
  position: absolute;
  right: 1.5%;
  bottom: 30%;
  margin: 0 20px;
  font-size: 20px;
  cursor: pointer;
}
.product_list .update{
  position: absolute;
  right: 1%;
  top: 30%;
  margin: 0 20px;
  font-size: 20px;
  cursor: pointer;
}
.product_list .trash:hover{
  color: #a805ad;
}
.product_list .update:hover{
  color: #a805ad;
}
.swiper {height: 100px;}
.swiper-slide img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.swiper-button-next:after, .swiper-button-prev:after
{
  font-size: 16px !important;
  color: #fff;
}
.swiper-scrollbar{
  width: 100% !important;
  left: 0 !important;
}
.swiper-scrollbar-drag{
  background: linear-gradient(45deg, #fe0929 0%,#a805ad 50%,#7601f9 100%) !important;
}