@charset "utf-8";
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
}
*{margin: 0;padding: 0;}
ul, li{list-style: none;}
body{
  font-family: 'Pretendard-Regular';
  font-size: 14px;
  background: #000;
  color: #fff;
}
header h1{
  text-align: center;
  margin: 10px 0;
}
header img{
  width: 250px;
}
footer{
  margin-top: 100px;
}
footer address{
  margin-top: 50px;
  border-top: 1px solid #fff;
  text-align: center;
  line-height: 60px;
}